import ProfileImageANDTitle from './atomicComponents/profileImage-title'
import AuthInputField from './atomicComponents/authInputField'

export default function ProfileInfoWrapper({ setDone }) {
  return (
    <div className="profile-personal-info-wrapper">
      <div className="profile-personal-info-edit-button-wrapper"></div>
      <ProfileImageANDTitle />
      <div className="profile-inputs-holders-wrapper">
        <div className="profile-inputs-holder-top">
          <div className="profile-inputs-holder-top-left">
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="First Name">
                First Name
              </label>
              <AuthInputField
                name="First Name"
                placeholderText={'Type here...'}
              />
            </div>
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="Mobile">
                Mobile Phone
              </label>
              <AuthInputField name="Mobile" placeholderText={'Type here...'} />
            </div>
          </div>
          <div className="profile-inputs-holder-top-right">
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="Last Name">
                Last Name
              </label>
              <AuthInputField
                name="Last Name"
                placeholderText={'Type here...'}
              />
            </div>
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="Email">
                Email
              </label>
              <AuthInputField name="Email" placeholderText={'Type here...'} />
            </div>
          </div>
        </div>
        {/* <=== Top section ends here ===>  */}
        <div className="profile-input-field-holder-long">
          <label className="profile-formm-label" for="Address">
            Address
          </label>
          <AuthInputField
            name="town"
            placeholderText={'Type here'}
            className={'long'}
          />
        </div>
        {/* <=== Bottom section starts here ===> */}
        <div className="profile-inputs-holder-top">
          <div className="profile-inputs-holder-top-left">
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="town">
                City/town
              </label>
              <AuthInputField
                name="First Name"
                placeholderText={'Type here...'}
              />
            </div>
            <div className="profile-input-field-holder-last">
              <label className="profile-formm-label" for=" Password">
                Password
              </label>
              <AuthInputField name="Mobile" placeholderText={'Type here...'} />
            </div>
          </div>
          <div className="profile-inputs-holder-top-right">
            <div className="profile-input-field-holder">
              <label className="profile-formm-label" for="Profession">
                Profession
              </label>
              <AuthInputField
                name="Last Name"
                placeholderText={'Type here...'}
              />
            </div>
          </div>
        </div>
      </div>
      <button
        className="profile-personal-info-button"
        onClick={() => {
          setDone(0)
        }}
      >
        SAVE CHANGES
      </button>
    </div>
  )
}
