import { useEffect, useState } from 'react'

export default function TypeTag({ title }) {
  const [color, setColor] = useState('')
  const [sizeInPx, setSizeInPx] = useState(0)

  useEffect(() => {
    const mapTagType = () => {
      switch (title) {
        case 'bug':
          setColor('red')
          setSizeInPx(50)
          break
        case 'closed':
          setColor('#E9C712')
          setSizeInPx(80)
          break
        case 'help wanted':
          setColor('green')
          setSizeInPx(140)
          break
        case 'software':
          setColor('blue')
          setSizeInPx(140)
          break
        default:
          setColor('gray')
          setSizeInPx(50)
      }
    }

    mapTagType()
  }, [title])

  return (
    <div>
      <div
        className={`type-tag  ${title.toLowerCase()}`}
        style={{ backgroundColor: color, width: `${sizeInPx}px` }}
      >
        {title.toUpperCase()}
      </div>
    </div>
  )
}
