import ButtonColoredSmall from './atomicComponents/buttonColoredSmall'

export default function FooterInput() {
  return (
    <div className="footer-input-component-wrapper">
      <input className="footer-input-field" placeholder="Email" />
      <div className="footer-input-button-holder">
        <ButtonColoredSmall buttonText={'submit'} path={' '} />
      </div>
    </div>
  )
}
