import ButtonColored from './atomicComponents/buttonColored'
import ButtonOutline from './atomicComponents/buttonOutline'

export default function HomeTopSectionComponent() {
  return (
    <div className="home-page-hero-holder">
      <div className="home-page-hero-text-holder">
        <h1 className="home-page-hero-heading">
          Ohmio - Your Smart Home Companion
        </h1>
        <p className="home-page-hero-text">
          Experience a Smarter Way of Living
        </p>
        <div className="home-page-hero-buttons-holder">
          <ButtonColored buttonText={'Learn more '} path={'/learnMore'} />
          <ButtonOutline
            buttonText={'Get started today'}
            path={'/allProducts'}
          />
        </div>
      </div>
      <img src={require('../Public/Smart_home-amico.png')} alt="Welcome hero" />
    </div>
  )
}
