import { useNavigate } from 'react-router-dom'

export default function ButtonColored({ buttonText, path }) {
  const navigate = useNavigate()

  const redirect = (path) => {
    navigate(path)
  }

  return (
    <>
      <button
        aria-label="button"
        className="button-colored"
        onClick={() => redirect(path)}
      >
        {buttonText.toUpperCase()}
      </button>
    </>
  )
}
