import AllProductsComponent from './atomicComponents/allProductsComponent'

export default function SensorsList() {
  return (
    <div className="sensors-holder">
      <h2 className="sensors-heading">Sensors</h2>
      <div className="product-type-list">
        <AllProductsComponent
          num={1}
          title={'Aqara Temperature and Humidity Sensor'}
          price={101}
          bodyText={'Hygrometer, Thermometer'}
        />
        <AllProductsComponent
          num={2}
          title={'AeroVane - Smart Wind Sensor'}
          price={500}
          bodyText={'Anemometer'}
        />
        <AllProductsComponent
          num={3}
          title={'FloodGuard - Water Level Sensor'}
          price={366}
          bodyText={'Hygrometer'}
        />
      </div>
    </div>
  )
}
