export default function LearnMoreSecondSection() {
  return (
    <div className="learn-more-second-section-holder">
      <img
        className="learn-more-second-section-image"
        src={require('../Public/iot.png')}
        alt="IoT"
      />
      <article className="learn-more-second-section-text-holder">
        <h1 className="learn-more-second-section-heading">What is Ohmo?</h1>
        <p className="learn-more-second-section-text">
          Ohmio is a pioneering smart home automation open ecosystem,
          revolutionizing the way we experience and interact with our living
          spaces. It seamlessly integrates cutting-edge technology to create an
          intelligent environment where users can effortlessly control and
          customize various aspects of their homes, from lighting and climate to
          security and energy efficiency.
        </p>
      </article>
    </div>
  )
}
