import placeholder from '../../Public/placeholder-image.png'
import ButtonOutlineSmall from './buttonOutlineSmall'
export default function BestSellersComponent({ num, title, bodyText, price }) {
  const mapFilePaths = (num) => {
    if (num === 1) {
      return placeholder
    }
    if (num === 2) {
      return placeholder
    }
    if (num === 3) {
      return placeholder
    }
  }
  return (
    <div className="best-sellers-element-holder">
      <img
        className="best-sellers-element-images"
        src={mapFilePaths(num)}
        alt="Best sellers"
      />
      <div className="best-sellers-element-text-holder">
        <h1 className="best-sellers-element-heading">{title}</h1>
        <p className="best-sellers-element-text">{bodyText}</p>
        <h2 className="best-sellers-element-price">{price}DKK</h2>
        <div className="best-sellers-element-button-holder">
          <ButtonOutlineSmall buttonText={'add to cart'} path={''} />
        </div>
      </div>
    </div>
  )
}
