import icon1 from '../../Public/3dicons_1.png'
import icon2 from '../../Public/3dicons_2.png'
import icon3 from '../../Public/3dicons_3.png'

/* By using this function  */

export default function OhmioFeatures({ num, title, bodyText }) {
  //Dynamically renders the image based on params
  const mapFilePaths = (num) => {
    if (num === 1) {
      return icon1
    }
    if (num === 2) {
      return icon2
    }
    if (num === 3) {
      return icon3
    }
  }
  return (
    <div className="ohmio-feature-element">
      <img
        className="ohmio-feature-element-img"
        src={mapFilePaths(num)}
        alt="Features section"
      />
      <div className="ohmio-feature-element-text-holder">
        <h1 className="ohmio-feature-element-heading">{title}</h1>
        <p className="ohmio-feature-element-text">{bodyText}</p>
      </div>
    </div>
  )
}
