import TypeTag from './atomicComponents/typeTag'
import ChatBubbleOutlineOutlinedIcon from '@mui/icons-material/ChatBubbleOutlineOutlined'
import RadioButtonCheckedOutlinedIcon from '@mui/icons-material/RadioButtonCheckedOutlined'
export default function IssueReportDescriptionComponent({
  comment,
  issueType,
}) {
  return (
    <div className="report-description-holder">
      <div className="report-description-content-wrapper">
        <div className="report-description-icon-open-holder">
          <RadioButtonCheckedOutlinedIcon />
        </div>
        <div className="report-description-content">
          <div className="report-description-open-icon-heading-holder">
            <h1 className="report-description-heading">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit
            </h1>
          </div>
          <p className="report-description-text">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua.
          </p>
        </div>
        <div className="report-description-icons-holder">
          <div className="report-description-tag-holder">
            <TypeTag title={issueType} />
          </div>
          {comment ? (
            <div className="report-description-comment-icon-holder">
              <ChatBubbleOutlineOutlinedIcon /> 1
            </div>
          ) : (
            ''
          )}
        </div>
      </div>
    </div>
  )
}
