import AllProductsComponent from './atomicComponents/allProductsComponent'

export default function ActuatorsList() {
  return (
    <div className="actuators-holder">
      <h2 className="actuators-heading">Actuators</h2>
      <div className="product-type-list">
        <AllProductsComponent
          num={6}
          title={'ClimateMaster - HVAC System Actuator'}
          price={600}
          bodyText={'HVAC system actuator'}
        />
        <AllProductsComponent
          num={4}
          title={'AquaFlow - Smart Water Valve Actuator'}
          price={458}
          bodyText={'Smart water valve actuator'}
        />
        <AllProductsComponent
          num={5}
          title={'LumiAdjust - Smart Lighting Control Actuator'}
          price={250}
          bodyText={'Smart lighting controller'}
        />
      </div>
    </div>
  )
}
