import ButtonColored from './atomicComponents/buttonColored'
export default function HomeSecondSectionComponent() {
  return (
    <div className="home-page-second-section-holder">
      <div className="home-page-second-section-image-holder">
        <img
          className="mobile-phone"
          src={require('../Public/mobile.png')}
          alt="Download app"
        />
      </div>
      <div className="home-page-second-section-text-holder">
        <p className="home-page-second-section-text-description">
          GET THE OHMIO APP
        </p>
        <h1 className="home-page-second-section-heading">
          Ohmio App - Your Gateway to Smart Living
        </h1>
        <p className="home-page-second-section-text">
          Elevate your smart home experience with the Omio app. Download now for
          seamless control and automation.
        </p>
        <div className="home-page-second-section-button-holder">
          <ButtonColored buttonText={'download now'} path={'/downloadAppNow'} />
        </div>
      </div>
    </div>
  )
}
