import IssueReportDescriptionComponent from '../Components/issueReportDescriptionComponent'
export default function BugsAndUpdatesComponent() {
  return (
    <div>
      <div>
        <div className="description-components-holder">
          <IssueReportDescriptionComponent comment={false} issueType={'bug'} />
          <IssueReportDescriptionComponent
            comment={true}
            issueType={'software'}
          />
          <IssueReportDescriptionComponent
            comment={false}
            issueType={'help wanted'}
          />
          <IssueReportDescriptionComponent
            comment={true}
            issueType={'closed'}
          />
          <IssueReportDescriptionComponent comment={false} issueType={'bug'} />
          <IssueReportDescriptionComponent
            comment={true}
            issueType={'closed'}
          />
          <IssueReportDescriptionComponent
            comment={false}
            issueType={'closed'}
          />
          <IssueReportDescriptionComponent
            comment={true}
            issueType={'software'}
          />
        </div>
      </div>
    </div>
  )
}
