import OhmioLogo from "../Public/OMIO_logo-removebg-preview.png"
import AuthInputField from "./atomicComponents/authInputField"
import ButtonColored from "./atomicComponents/buttonColored"
import ButtonOutline from "./atomicComponents/buttonOutline"

export default function AuthForm({signUp}) {
    return (
        <>
        {signUp ? 
        <div className="auth-formm-out-wrapper">
            <div className="auth-form-left-side">
                <img src={OhmioLogo} className="ohmio-logo-auth-form" alt="Ohmio Logo"/>
                <h2 className="auth-form-heading-big">Hello, Stranger!</h2>
                <h3 className="auth-form-heading-small">Here you can create your account</h3>
                <p className="auth-form-text-small">Already have one?</p>
                <ButtonOutline buttonText={"Sign in"} path={"authSignIn"}/>
            </div>
            <div className="auth-form-right-side">
                <div className="auth-form-inputs-holder">
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="username">Username</label>
                       <AuthInputField name="username" placeholderText={"Enter your username"} />
                    </div>
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="password">Password</label>
                        <AuthInputField name="password" placeholderText={"Set a password"} />
                    </div>
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="re-password">Password</label>
                        <AuthInputField name="re-password" placeholderText={"Repeat your password"} />
                    </div>
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="email">Email</label>
                        <AuthInputField name="email" placeholderText={"Your Email"} />
                    </div>
                </div>
                <ButtonColored buttonText={"Sign Up"} path={""}/>
            </div>
        </div>
        :
        <div className="auth-formm-out-wrapper">
            <div className="auth-form-left-side">
                <img src={OhmioLogo} className="ohmio-logo-auth-form" alt="Ohmio Logo"/>
                <h2 className="auth-form-heading-big">Welcome Back</h2>
                <h3 className="auth-form-heading-small">We missed you</h3>
            </div>
            <div className="auth-form-right-side">
                <div className="auth-form-inputs-holder">
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="username">Username</label>
                       <AuthInputField name="username" placeholderText={"Enter your username"} />
                    </div>
                    <div className="auth-form-input-field-holder">
                        <label className="auth-formm-label" for="password">Password</label>
                        <AuthInputField name="password" placeholderText={"Set a password"} />
                    </div>
                </div>
                <ButtonColored buttonText={"Sign In"} path={""}/>
            </div>
        </div>}
        </>
    )
}