import AllProductsComponent from './atomicComponents/allProductsComponent'

export default function DevicesList() {
  return (
    <div className="devices-holder">
      <h2 className="devices-heading">Devices</h2>
      <div className="product-type-list">
        <AllProductsComponent
          num={7}
          title={'Ohmio device'}
          price={998}
          bodyText={'Photovoltaic energy monitor'}
        />
      </div>
    </div>
  )
}
