export default function DownloadAppNowComponent() {
  return (
    <div className="download-now-holder">
      <img
        className="phone-download"
        src={require('../../Public/phone.png')}
        alt="Download App Now"
      />
      <div className="download-now-left-side">
        <h1 className="download-now-heading">
          Turn Your Home into the Heartbeat of Innovation!
        </h1>
        <div className="download-now-buttons-holder">
          <img
            src={require('../../Public/google-play-badge.png')}
            className="download-cta-badge download-cta-badge-googleStore"
            alt="Download App Now"
          />
          <img
            src={require('../../Public/app-store-badge.png')}
            className="download-cta-badge download-cta-badge-appStore"
            alt="Download App Now"
          />
        </div>
      </div>
    </div>
  )
}
