import windSensor from '../../Public/wind-sensor.jpg'
import waterLevelSensor from '../../Public/water-level-sensor.jpg'
import temperatureHumiditySensor from '../../Public/temperature-humidity-sensor.jpg'
import lightActuator from '../../Public/light-actuator.jpg'
import senseEnergyMonitor from '../../Public/sense-energy-monitor.png'
import humidityTemp from '../../Public/humidity-temp.jpg'
import ohmioDevice from '../../Public/ohmio-device.jpg'
import placeholder from '../../Public/placeholder-image.png'
import ButtonOutlineSmall from './buttonOutlineSmall'

export default function AllProductsComponent({ num, title, bodyText, price }) {
  const mapFilePaths = (num) => {
    if (num === 0) {
      return placeholder
    }
    if (num === 1) {
      return temperatureHumiditySensor
    }
    if (num === 2) {
      return windSensor
    }
    if (num === 3) {
      return waterLevelSensor
    }
    if (num === 4) {
      return humidityTemp
    }
    if (num === 5) {
      return lightActuator
    }
    if (num === 6) {
      return senseEnergyMonitor
    }
    if (num === 7) {
      return ohmioDevice
    }
  }
  return (
    <div className="all-products-element-holder">
      <img
        className="all-products-element-images"
        src={mapFilePaths(num)}
        alt={title}
      />
      <div className="all-products-element-text-holder">
        <h1 className="all-products-element-heading">{title}</h1>
        <p className="all-products-element-text">{bodyText}</p>
        <h2 className="all-products-element-price">{price}DKK</h2>
        <div className="all-products-element-button-holder">
          <ButtonOutlineSmall buttonText={'add to cart'} path={''} />
        </div>
      </div>
    </div>
  )
}
