import ProfileInfoBLOC from '../Components/profile-info-wrapper-BLOC'
import ProfileTabsMenu from '../Components/profileTabsMenu'

export default function Profile() {
  return (
    <div className="page-main-content-wrapper profile-page-main-content-wrapper">
      <div className="profile-page-content-inner-wrapper">
        <div className="profile-page-left-side-wrapper">
          <div className="profile-page-heading-holder">
            <h1 className="profile-page-heading">My account</h1>
            <p className="profile-page-text">
              All your personal details in one place
            </p>
          </div>
          <ProfileTabsMenu />
        </div>
        <div className="profile-page-right-side-wrapper">
          <ProfileInfoBLOC />
        </div>
      </div>
    </div>
  )
}
