import TabsComponent from '../Components/atomicComponents/tabsComponent'
import RecentlyViewed from '../Components/recentlyViewed'
import SimilarItems from '../Components/similarItems'
export default function AllProducts() {
  return (
    <div className="all-products-container">
      <div className="all-prducts-image-holder">
        <img
          className="quote"
          src={require('../Public/all-products-hero-image.png')}
          alt="Quote"
        />
        <div className="content-container">
          <div className="tabs-and-dropdown-container">
            <TabsComponent />
          </div>
          <RecentlyViewed />
          <SimilarItems />
        </div>
      </div>
    </div>
  )
}
