import profilePicture from '../../Public/profile-picture.png'

export default function ProfileImageANDTitle() {
  return (
    <div className="profile-image-title-component-wrapper">
      <img className="profile-image" src={profilePicture} alt="profile" />
      <h3 className="profile-name-heading">Your Name</h3>
      <p className="profile-profession-heading">Profession</p>
    </div>
  )
}
