import { useState } from 'react'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import InventoryIcon from '@mui/icons-material/Inventory'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import BookmarkIcon from '@mui/icons-material/Bookmark'

export default function ProfileTabsMenu() {
  const [clikedTab, setClickedTab] = useState(0)

  const clickEvent = (num) => {
    setClickedTab(num)
    return
  }
  return (
    <div className="profile-tabs-menu-wrapper">
      <button
        className="profile-menu-tab profile-menu-tab-first"
        onClick={() => clickEvent(1)}
        style={
          clikedTab === 1 ? { backgroundColor: '#7B44B1', color: 'white' } : {}
        }
      >
        <AccountCircleIcon style={{ marginRight: '8px' }} />
        My account
      </button>
      <button
        className="profile-menu-tab "
        onClick={() => clickEvent(2)}
        style={
          clikedTab === 2 ? { backgroundColor: '#7B44B1', color: 'white' } : {}
        }
      >
        <InventoryIcon style={{ marginRight: '8px' }} />
        My Orders
      </button>
      <button
        className="profile-menu-tab "
        onClick={() => clickEvent(3)}
        style={
          clikedTab === 3 ? { backgroundColor: '#7B44B1', color: 'white' } : {}
        }
      >
        <ShoppingCartIcon style={{ marginRight: '8px' }} />
        Shopping Cart
      </button>
      <button
        className="profile-menu-tab profile-menu-tab-last"
        onClick={() => clickEvent(4)}
        style={
          clikedTab === 4 ? { backgroundColor: '#7B44B1', color: 'white' } : {}
        }
      >
        <BookmarkIcon style={{ marginRight: '8px' }} />
        Saved posts
      </button>
    </div>
  )
}
