// App.js
import React from "react";
import { Route, Routes } from "react-router-dom";
import "./App.css";
import { Navigate } from "react-router-dom";
import NavBar from "./Components/nav-bar";
import Home from "./Pages/home";
import AllProducts from "./Pages/allProducts";
import DownloadAppNow from "./Pages/downloadAppNow";
import BugsAndupdates from "./Pages/bugsAndupdates";
import AuthenticationPage from "./Pages/AuthenticationPage";
import Profile from "./Pages/profile";
import LearnMore from "./Pages/learnMore";
import Footer from "./Components/Footer";

function App() {
  return (
    <div className="app-holder">
      <div className="blur-circle-purple"></div>
      <NavBar />
      <div className="blur-circle-blue"></div>
      <Routes>
        <Route path="*" element={<Navigate to="/" />} />
        <Route path="/" element={<Home />} />
        <Route path="/allProducts" element={<AllProducts />} />
        <Route path="/downloadAppNow" element={<DownloadAppNow />} />
        <Route path="/bugs-updates" element={<BugsAndupdates />} />
        <Route path="/auth" element={<AuthenticationPage signUp={true} />} />
        <Route
          path="/auth/authSignIn"
          element={<AuthenticationPage signUp={false} />}
        />
        <Route path="/profile" element={<Profile />} />
        <Route path="/learnMore" element={<LearnMore />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
