import { useState } from 'react'
import ProfileInfoWrapper from './profileInfoWrapper'
import ProfileInfoWrapperFirst from './profileInfoWrapperFirst'

export default function ProfileInfoBLOC() {
  /*BLOC - business logic component */
  const [done, setDone] = useState(0)
  return (
    <>
      {done === 0 ? (
        <ProfileInfoWrapperFirst setDone={setDone} />
      ) : (
        <ProfileInfoWrapper setDone={setDone} />
      )}
    </>
  )
}
