import React, { useState } from 'react'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import Box from '@mui/material/Box'
import SensorsList from '../SensorsList'
import ActuatorsList from '../ActuatorsList'
import AllProductsList from '../allProductsList'
import DevicesList from '../DevicesList'
import SortByDropdown from './sortByDropdown'

export default function TabsComponent() {
  const [value, setValue] = useState('one')

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }

  return (
    <div>
      <Box
        className="tabWrapper"
        sx={{ display: 'flex', justifyContent: 'center' }}
      >
        {/* I added Flexbox properties for centering and adding space between tabs */}
        <Tabs value={value} onChange={handleChange}>
          <Tab
            value="one"
            label="All products"
            className="tabButton"
            sx={{
              /* I used the sx prop to conditionally apply styles based on the selected state  */
              width: '200px',
              height: '51px',
              borderRadius: '30px',
              border: '2px solid #AABBDD',
              backgroundColor: 'transparent',
              fontSize: '15pt',
              color: 'white',
              cursor: 'pointer',
              '&.Mui-selected': {
                backgroundColor: '#7B44B1',
                border: 'none',
              },
              marginRight: '16px',
            }}
          />
          <Tab
            value="two"
            label="Devices"
            className="tabButtonTwo"
            sx={{
              width: '200px',
              height: '51px',
              borderRadius: '30px',
              border: '2px solid #AABBDD',
              backgroundColor: 'transparent',
              fontSize: '15pt',
              color: 'white',
              cursor: 'pointer',
              '&.Mui-selected': {
                backgroundColor: '#7B44B1',
                border: 'none',
              },
              marginRight: '16px',
            }}
          />
          <Tab
            value="three"
            label="Sensors"
            className="tabButtonThree"
            sx={{
              width: '200px',
              height: '51px',
              borderRadius: '30px',
              border: '2px solid #AABBDD',
              backgroundColor: 'transparent',
              fontSize: '15pt',
              color: 'white',
              cursor: 'pointer',
              '&.Mui-selected': {
                backgroundColor: '#7B44B1',
                border: 'none',
              },
              marginRight: '16px',
            }}
          />
          <Tab
            value="four"
            label="Actuators"
            className="tabButtonFour"
            sx={{
              width: '200px',
              height: '51px',
              borderRadius: '30px',
              border: '2px solid #AABBDD',
              backgroundColor: 'transparent',
              fontSize: '15pt',
              color: 'white',
              cursor: 'pointer',
              '&.Mui-selected': {
                backgroundColor: '#7B44B1',
                border: 'none',
              },
              marginRight: '16px',
            }}
          />
        </Tabs>
        <SortByDropdown />
      </Box>
      {value === 'one' ? (
        <AllProductsList />
      ) : value === 'two' ? (
        <DevicesList />
      ) : value === 'three' ? (
        <SensorsList />
      ) : value === 'four' ? (
        <ActuatorsList />
      ) : (
        <h2>All products</h2>
      )}
    </div>
  )
}
