import OhmioFeatures from './atomicComponents/ohmioFeatures'
export default function ohmioFeaturesAll() {
  return (
    <div className="home-page-third-section-holder">
      <OhmioFeatures
        num={1}
        title={'Seamless Integration'}
        bodyText={
          'Ohmio seamlessly connects with a wide range of smart devices, allowing you to control and automate lighting, temperature and more from a single, user-friendly interface'
        }
      />
      <OhmioFeatures
        num={2}
        title={'Energy Efficiency'}
        bodyText={
          "Enhance energy efficiency in your home with Ohmio's smart automation, helping you reduce utility bills and minimize environmental impact."
        }
      />
      <OhmioFeatures
        num={3}
        title={'Remote Access'}
        bodyText={
          "Experience the convenience of managing your home from anywhere, whether you're at home or away, through Ohmio's intuitive control system."
        }
      />
    </div>
  )
}
