import ProfileImageANDTitleFirst from './atomicComponents/profileImage-titleFirst'

export default function ProfileInfoWrapperFirst({ setDone }) {
  return (
    <div className="profile-personal-info-wrapper">
      <div className="profile-personal-info-edit-button-wrapper">
        <button
          className="profile-personal-info-edit-button"
          onClick={() => {
            setDone(1)
          }}
        >
          EDIT
        </button>{' '}
      </div>

      <ProfileImageANDTitleFirst />
      <div className="profile-info-first-holder-wrapper">
        <div className="profile-info-first-holder-top">
          <div className="profile-info-first-holder-top-left">
            <div className="profile-info-first-holder">
              <h1 className="profile-info-first-title">Mobile phone</h1>
              <p className="profile-info-first-text">0987654323456</p>
            </div>
          </div>
          <div className="profile-info-first-holder-top-right">
            <div className="profile-info-first-holder">
              <h1 className="profile-info-first-title">Email</h1>
              <p className="profile-info-first-text">ariawilliams@gmail.com</p>
            </div>
          </div>
        </div>
        {/* <=== Top section ends here ===>  */}
        <div className="profile-info-first-holder-long">
          <div className="profile-info-first-holder">
            <h1 className="profile-info-first-title">Address</h1>
            <p className="profile-info-first-text">
              123 Pine Street, Meadowville, USA
            </p>
          </div>
        </div>
        {/* <=== Bottom section starts here ===> */}
        <div className="profile-info-first-holder-top">
          <div className="profile-info-first-holder-top-left-last">
            <div className="profile-info-first-holder">
              <h1 className="profile-info-first-title">City/Town</h1>
              <p className="profile-info-first-text"> Meadowville</p>
            </div>
          </div>
          <div className="profile-info-first-holder-top-right">
            <div className="profile-info-first-holder">
              <h1 className="profile-info-first-title">Profession</h1>
              <p className="profile-info-first-text">Botanical illustrator</p>
            </div>
          </div>
          {/* <=== Bottom section ends here ===>  */}
        </div>
      </div>
    </div>
  )
}
