import profilePicture from '../../Public/profile-picture.png'

export default function ProfileImageANDTitleFirst() {
  return (
    <div className="profile-image-title-component-wrapper-first">
      <img className="profile-image-first" src={profilePicture} alt="profile" />
      <h3 className="profile-name-heading">Aria Williams</h3>
      <p className="profile-profession-heading">Botanical illustrator</p>
    </div>
  )
}
